import { CourseModel } from '../models/CourseModel';
import { ProgramModel } from '../models/ProgramModel';
import { SpecialisationModel } from '../models/SpecialisationModel';
import { CourseState } from './CourseState';
import { ProgressionState } from './ProgressionState';


export class SearchDataState {
  constructor(
    private _courseState: CourseState,
    private _progressionState: ProgressionState,
  ) {}

  get courseSearchData(): CourseModel[] {
    return this._courseState.allCourses
  }
  get programSearchData(): ProgramModel[] {
    return this._progressionState.allPrograms
  }
  get specialisationSearchData(): SpecialisationModel[] {
    return this._progressionState.allSpecialisations
  }
}
